.contenedor {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 600px;
    background-color: #fff;
    padding-bottom: 30px;

}

.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}

.text{
    color: #3c009d;
    font-size: 48px;
    font-weight: 700;
}

.underline{
    width: 61px;
    height: 6px;
    background: #3c009d;
    border-radius: 9px;
}

.entradas{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.entrada{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 60px;
    background: #eaeaea;
    border-radius: 6px;
}

label {
    margin: 0px 30px;
}

input, select {
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
}

.enviar {
    display: flex;
    gap: 30px;
    margin: 60px auto;
}
.registrar, .cancelar{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: #4c00b4;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;

}

